import React, { useContext, useState, useEffect } from "react";
import { Button } from "@windmill/react-ui";
import { Link, Redirect, useHistory, useLocation } from "react-router-dom";
import AuthService from "../Services/AuthService";
import { AuthContext } from "../context/AuthContext";
import { SearchContext } from "../context/SearchContext";
import API from "../utils/API";
import Swal from "sweetalert2";
import { SidebarContext } from "../context/SidebarContext";
import {
  SearchIcon,
  MoonIcon,
  SunIcon,
  BellIcon,
  MenuIcon,
  OutlinePersonIcon,
  OutlineCogIcon,
  OutlineLogoutIcon,
} from "../icons";
import {
  Avatar,
  Badge,
  Input,
  Dropdown,
  DropdownItem,
  WindmillContext,
} from "@windmill/react-ui";
import { authenticate } from "passport";
import { set } from "mongoose";
// import e from "express";

function Header(props) {
  let location = useLocation();

  const { mode, toggleMode } = useContext(WindmillContext);
  const { toggleSidebar } = useContext(SidebarContext);

  const [isNotificationsMenuOpen, setIsNotificationsMenuOpen] = useState(false);
  const [isProfileMenuOpen, setIsProfileMenuOpen] = useState(false);

  function handleNotificationsClick() {
    setIsNotificationsMenuOpen(!isNotificationsMenuOpen);
  }

  function handleProfileClick() {
    setIsProfileMenuOpen(!isProfileMenuOpen);
  }

  const { isAuthenticated, user, setIsAuthenticated, setUser } = useContext(
    AuthContext
  );

  const [title, setTitle] = useState("");

  const onClickLogoutHandler = () => {
    AuthService.logout().then((data) => {
      if (data.success) {
        Swal.fire({
          icon: "success",
          title: "Successfully logged out",
          showConfirmButton: false,
          timer: 1000,
        });
        setUser(data.user);
        setIsAuthenticated(false);
        console.log("user logged out");
      } else {
        console.log("logout failed for some reason");
      }
    });
  };

  let results = "";

  const [newTitle, setNewTitle] = useState("");
  const [newAuthor, setNewAuthor] = useState("");
  const [search, setSearch] = useContext(SearchContext);

  const searchSubmitHandler = (event) => {
    event.preventDefault();
    API.SearchBooks(title)
      .then((res) => {
        // setTimeout(() => {
        //   addBook(res.data.items);
        // }, 500);
        addBook(res.data.items);
      })
      .catch((err) => console.log(err));
  };

  const addBook = (data) => {
    setSearch(data);
    console.log(search);
  };

  return (
    <header className="z-40 py-4 bg-white shadow-bottom dark:bg-gray-800">
      <div className="container flex items-center justify-between h-full px-6 mx-auto text-purple-600 dark:text-purple-300">
        <Link
          className="ml-6 mr-6 text-lg text-gray-500 dark:text-gray-200"
          to="/app/mypage"
        >
          {user.username}
        </Link>
        {/* <!-- Mobile hamburger --> */}
        <button
          className="p-1 mr-5 -ml-1 rounded-md lg:hidden focus:outline-none focus:shadow-outline-purple"
          onClick={toggleSidebar}
          aria-label="Menu"
        >
          <MenuIcon className="w-6 h-6" aria-hidden="true" />
        </button>
        {/* <div>
          {!isAuthenticated ? unauthenticatedNavBar() : authenticatedNavBar()}
        </div> */}
        {/* <!-- Search input --> */}
        <div className="flex justify-center flex-1 lg:mr-32">
          <div className="relative w-full max-w-xl mr-6 focus-within:text-purple-500">
            <div className="absolute inset-y-0 flex items-center pl-2">
              <SearchIcon className="w-4 h-4" aria-hidden="true" />
            </div>
            <form onChange={searchSubmitHandler}>
              <Input
                onChange={(event) => setTitle(event.target.value)}
                value={title}
                className="pl-8 text-gray-700"
                placeholder="Search for books, authors, users, etc."
                aria-label="Search"
              />
            </form>
          </div>
        </div>
        <ul className="flex items-center flex-shrink-0 space-x-6">
          {/* <!-- Theme toggler --> */}
          <li className="flex">
            <button
              className="rounded-md focus:outline-none focus:shadow-outline-purple"
              onClick={toggleMode}
              aria-label="Toggle color mode"
            >
              {mode === "dark" ? (
                <SunIcon className="w-5 h-5" aria-hidden="true" />
              ) : (
                  <MoonIcon className="w-5 h-5" aria-hidden="true" />
                )}
            </button>
          </li>
          {/* <!-- Notifications menu --> */}
          <li className="relative">
            <button
              className="relative align-middle rounded-md focus:outline-none focus:shadow-outline-purple"
              onClick={handleNotificationsClick}
              aria-label="Notifications"
              aria-haspopup="true"
            >
              <BellIcon className="w-5 h-5" aria-hidden="true" />
              {/* <!-- Notification badge --> */}
              <span
                aria-hidden="true"
                className="absolute top-0 right-0 inline-block w-3 h-3 transform translate-x-1 -translate-y-1 bg-red-600 border-2 border-white rounded-full dark:border-gray-800"
              ></span>
            </button>

            <Dropdown
              align="right"
              isOpen={isNotificationsMenuOpen}
              onClose={() => setIsNotificationsMenuOpen(false)}
            >
              <DropdownItem
                tag="a"
                href="/app/messages"
                className="justify-between"
              >
                <span>Messages</span>
                <Badge type="danger">13</Badge>
              </DropdownItem>
              <DropdownItem tag="a" href="#" className="justify-between">
                <span>Sales</span>
                <Badge type="danger">2</Badge>
              </DropdownItem>
              <DropdownItem onClick={() => alert("Alerts!")}>
                <span>Alerts</span>
              </DropdownItem>
            </Dropdown>
          </li>
          {/* <!-- Profile menu --> */}
          <li className="relative">
            <button
              className="rounded-full focus:shadow-outline-purple focus:outline-none"
              onClick={handleProfileClick}
              aria-label="Account"
              aria-haspopup="true"
            >
              <Avatar
                className="align-middle"
                src="https://i.imgur.com/epoovI9.jpg"
                alt=""
                aria-hidden="true"
              />
            </button>
            <Dropdown
              align="right"
              isOpen={isProfileMenuOpen}
              onClose={() => setIsProfileMenuOpen(false)}
            >
              <DropdownItem tag="a" href="/app/mypage">
                <OutlinePersonIcon
                  className="w-4 h-4 mr-3"
                  aria-hidden="true"
                />
                <span>Profile</span>
              </DropdownItem>
              {/* <DropdownItem tag="a" href="#">
                <OutlineCogIcon className="w-4 h-4 mr-3" aria-hidden="true" />
                <span>Settings</span>
              </DropdownItem> */}
              <Link to="/login">
                <DropdownItem onClick={onClickLogoutHandler}>
                  <OutlineLogoutIcon
                    className="w-4 h-4 mr-3"
                    aria-hidden="true"
                  />
                  <span>Log out</span>
                </DropdownItem>
              </Link>
            </Dropdown>
          </li>
        </ul>
      </div>
    </header>
  );
}

export default Header;
